<template>
    <div class="container60">
       <Header/>
       <div class="content10">
            <Sourcehead/>
       </div>
       <div class="footer10">
           <Footer/>
       </div>  
    </div>
</template>

<script>
import Header from '@/components/HomePage/header'
import Sourcehead from '@/components/Source/Sourcehead'
import Footer from '@/components/HomePage/Footer'
export default{
    mounted:function() {
        this.Getsession()  
        // this.$store.dispatch('Getsession01') 
    }, 
    methods:{
         Getsession() {
            // session验证
            console.log(this.$store.state.userid)   
            this.axios.get('/res/verify')
            .then(response =>{
                // this.formLogin.username01=response.data.data.username
                if(response.data.data){
                    this.$store.state.userid=response.data.data.userid
                }else{
                }
            }) 
        },
    },
    components:{
        Header,
        Footer,
        Sourcehead
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container60{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}
/* .container13 .container003{
    position: absolute;
    left: 0px;
    bottom: -142rem;   
} */
.content10 {
    flex: 1;
}
.footer10 {
    flex: 0;
}
</style>



